.text-block{
  display: flex;
  flex-direction: column;
  text-align: left;
  position: relative;

  font-family: 'Inter';
  font-style: normal;
  color: #0F303F;

  max-width: 627px;

  padding: 0 16px;

  &--main{
    max-width: 490px;
  }
}

.text-block__wrapper{
  margin: auto 0;
}


.text-block__title{
  font-weight: 500;
  font-size: 28px;
  line-height: 32px;
  text-transform: uppercase;

  padding-bottom: 12px;
}

.text-block__text{
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  padding-top: 20px;

  &--color-white{
    color: #F4F4F4;
  }

  &--fw-300{
    font-weight: 300;
  }
}

.text-block__collection{
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: .045em;
  text-transform: uppercase;

  margin-top: 25px;

  a{
    color: #eae9e8;
    transition: opacity $trans-default;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0; bottom: -5px;
      width: 0;
      height: 1.5px;
      background-color: #eae9e8;
      transition: width $trans-default, opacity $trans-default;
      border-radius: 5px;
    }

    @include hover-focus{
      opacity: 0.8;

      &::before{
        width: 100%;
        opacity: 0.8;
      }
    }
  }
}
